.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  margin-top: 2rem;
  margin-left: 20vw;
  margin-right: 20vw;
  padding-left:8vw;
  padding-right:8vw;
  padding-top:5vh;
  padding-bottom:5vh;
}
  
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.unlock {
  color: gray;
  font-weight: bold;
  font-style: italic;
}
